.header {
  height: 100%;
  background-color: rgb(110, 110, 179);
  padding: 20px;
  text-align: center;
}

.container {
  display: flex;
}

.weather-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  gap: 10px;
}

.weather-form__btn {
  padding: 10px;
  max-width: 100px;
  background-color: gold;
  border-radius: 10px;
  border: 0px;
}

.weather-form__btn:hover {
  background-color: rgb(225, 197, 36);
}

.sidebar {
  background: gray;
  flex: 0 1 20%;
  height: 100vh;
}

.main {
  background: rgb(163, 163, 178);
  flex: 1 1 auto;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 1 80%;
}

.icon {
  width: 20px;
  height: 20px;
}

.discription {
  display: flex;
  gap: 10px;
}
